        max-width: 203px;
    }
}
header {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 50px 0 126px;
    & .form-call {
        font-size: 11px;
        max-width: 245px;
        height: 51px;
    }
    & .navigation {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        max-width: 700px;
        & .lang {
            position: relative;
            width: 65px;
            margin-left: 40px;
            &  div { 
                font-family: 'Montserrat-Regular';
                font-size: 20px;
                text-transform: uppercase;
                color: #ffffff;
                transition: 0.3s ease;
                &.lang-trigger {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    cursor: pointer;
                    font-weight: bold;
                    color: #f44776;
                    & img {
                        width: 100%;
                        max-width: 14px;
                        margin-left: 8px;
                        transition: 0.5s ease;
                    }
                }
            }
            &-items {
                position: absolute;
                top: 100%;
                left: 4px;
                padding: 10px;
                background: rgba(244, 71, 118, 0.5);
                border-radius: 8px;
                transition: 0.5s ease;
                opacity: 0;
                z-index: -10;
                & > a {
                    display: block;
                    text-align: center;
                    color: #ffffff;
                    transition: 0.3s ease;
                    &:hover {color: rgb(255, 186, 205);}
                    &:not(:last-child) {padding-bottom: 10px;}
                }
            }
            &.active {
                & .lang-items {
                    opacity: 1;
                    z-index: 10;
                }
                & .lang-trigger {
                    & img {
                        transform: rotate(90deg);
                    }
                }
            }
        }
    }
    & nav {
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 100%;
        max-width: 623px;
        & a {
            font-family: 'Montserrat-Regular';
            font-size: 20px;
            text-transform: uppercase;
            color: #ffffff;
            padding: 0 5px;
            transition: 0.3s ease;
            &:hover {color: #f44776;}
        }
    }
}

