            height: calc(100% - 85px);
            padding: 30px 15px 20px;
            background: #ffffff;
            border-radius:0 0 15px 15px;
            &-top {
                padding-bottom: 5px;
                padding-left: 30px;
                font-family: 'Montserrat-Regular';
                font-size: 18px;
                font-weight: bold;
                color: #0e0846;
            }
            & ul {
                list-style: none;
                padding-left: 45px;
                padding-bottom: 60px;
                & li {
                    position: relative;
                    font-family: 'Montserrat-Regular';
                    font-size: 18px;
                    line-height: 25px;
                    color: #0e0846;
                    &:not(:last-child) {padding-bottom: 26px;}
                    &:before {
                        content: '';
                        display: block;
                        position: absolute;
                        top: 8px;
                        left: -16px;
                        width: 7px;
                        min-width: 7px;
                        height: 7px;
                        background: #fe4041;
                        border-radius: 50%; 
                    }
                }
        
            }
        }
    }
}


.form {
    padding: 290px 0 270px;
    background-image: url('../images/form-bg.png');
    background-size: cover;
    background-repeat: no-repeat;
    & .title,
    & .subtitle {text-align: center;}
    & .title {padding-bottom: 30px;}
    & .subtitle {padding-bottom: 70px;}
    & form {
        width: 100%;
        max-width: 1100px;
        margin: 0 auto;
    }
    &-fields {
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding-bottom: 35px;
        & input {
            max-width: 345px;
            font-size: 17px;
            &:not(:last-child) {margin-right: 10px;}
        }
    }
    &-subtitle {
        padding-bottom: 30px;
        font-family: 'Montserrat-Regular';
        font-size: 28px;
        text-align: center;
        font-weight: bold;
        color: #ffffff;
    }
    &-radio {
        display: flex;
        align-items: center;
        justify-content: space-between;
        flex-flow: row wrap;
        padding-bottom: 72px;
        & label {
            cursor: pointer;
            display: flex;
            align-items: center;
            justify-content: flex-start;
            width: 100%;
            max-width: 330px;
            padding: 0 5px 25px 0;
            & input {display: none;}
            & .form-radio-btn {
                position: relative;
                width: 28px;
                min-width: 28px;
                height: 28px;
                margin-right: 30px;
                border: 1px solid #ffffff;
                &::before {
                    content: '';
                    position: absolute;
                    top: 3px;
                    left: 3px;
                    display: block;
                    width: calc(100% - 6px);
                    height: calc(100% - 6px);
                    background: #FE2965;
                    transition: 0.5s ease;
                    opacity: 0;
                }
            }
            & .form-radio-name {
                font-family: 'Montserrat-Regular';
                font-size: 21px;
                color: #ffffff;
            }
        }
        & label input:checked + .form-radio-btn:before {opacity: 1;}

    }
    & .textarea-field {
        width: 100%;
        max-width: 100%;
        height: 214px;
        margin-bottom: 30px;
        padding: 30px 35px 20px 30px;
        background: #ffffff;
        border-radius: 15px;
        & textarea {
            height: 100%;
            padding: 0 15px 0 0;
            font-size: 17px;
            border: none;
            &::-webkit-scrollbar-button {
                background-image:url('');
                background-repeat:no-repeat;
                width:4px;
                height:0px
                }
                
            &::-webkit-scrollbar-track {
                background-color:#0A0246
                }
                
            &::-webkit-scrollbar-thumb {
                -webkit-border-radius: 0px;
                border-radius: 0px;
                background-color:#FF2A5C;
                }
                
            &::-webkit-scrollbar-thumb:hover{
                background-color:#FF2A5C;
                }
                
            &::-webkit-resizer{
                background-image:url('');
                background-repeat:no-repeat;
                width:4px;
                height:10px
                }
                
            &::-webkit-scrollbar{
                width: 2px;
                }
        }
    }
    & .field-href {
        width: 100%;
        max-width: 100%;
        margin-bottom: 92px;
        font-size: 17px;
    }
    & .btn {
        margin: 0 auto;
        font-size: 17px;
    }
}















