.footer {
    padding-top: 62px;
    background: #14081F;
    & .wrapper {
        display: flex;
        flex-flow: row wrap;
        justify-content: space-between;
        align-items: flex-start;
    }
    &-column {
        width: 100%;
        max-width: 290px;
        &-txt {
            font-family: 'Open Sans Regular';
            font-size: 14px;
            color: white;
            opacity: 0.75;
        }
        &-head {
            font-family: 'Rubik-Medium', sans-serif;
            font-size: 22px;
            color: #FFFFFF;
            padding-bottom: 28px;
            padding-top: 60px;
        }
        &-1 {
            & > img {margin-bottom: 31px;}
            & p {
                margin-bottom: 30px;
            }
            &-soc {
                display: flex;
                align-items: center;
                justify-content: flex-start;
                width: 100%;
                opacity: 1 !important;
                & a {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    width: 28px;
                    height: 28px;
                    margin-right: 15px;
                    border-radius: 50%;
                    background: rgba(124, 82, 156, 0.35);
                    opacity: 1 !important;
                    transition: 0.3s ease;
                    &:hover{
                        background: #7C529C;
                    }
                }
            }
        }
        &-2 {
            &-contact {
                display: flex;
                align-items: flex-start;
                margin-bottom: 15px;
                &-img {
                    display: flex;
                    align-items: center;
                    justify-content: center;
                    min-width: 18px;
                    margin-top: 3px;
                    margin-right: 10px;
                }
            }
        }
        &-3 {
            & label {
                position: relative;
                display: flex;
                align-items: center;
                justify-content: space-between;
                margin-bottom: 25px;
                width: 100%;
                max-width: 285px;
                & img {
                    position: absolute;
                    right: 0;
                    top: 4px;
                    &:nth-child(3) {
                        display: none;
                    }
                }
                & input {
                    width: 100%;
                    background: none;
                    font-family: 'Open Sans Regular';
                    font-size: 14px;
                    color: white;
                    opacity: 0.75;
                    border-bottom: 1px solid rgba(255, 255, 255, 0.5);
                    padding-bottom: 3px;
                    &::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
                        color: white;
                        opacity: 0.75;
                    }
                      
                    &:-ms-input-placeholder { /* Internet Explorer 10-11 */
                        color: white;
                        opacity: 0.75;
                    }
                      
                    &::-ms-input-placeholder { /* Microsoft Edge */
                        color: white;
                        opacity: 0.75;
                    }
                    &:focus {
                        border-bottom: 1px solid #FD226A;
                        color: white;
                        opacity: 1;
                        & + img {
                            display: none;
                            & + img {
                                display: inline-block;
                            }
                        }
                        &::placeholder { /* Chrome, Firefox, Opera, Safari 10.1+ */
                            color: white;
                            opacity:1;
                        }
                          
                        &:-ms-input-placeholder { /* Internet Explorer 10-11 */
                            color: white;
                            opacity:1;
                        }
                          
                        &::-ms-input-placeholder { /* Microsoft Edge */
                            color: white;
                            opacity:1;
                        }
                    }
                }
                
            }
            &  input[type="submit"] {
                width: 100%;
                max-width: 142px;
                padding: 11px 5px;
                font-family: 'Rubik-Medium', sans-serif;
                color: white;
                font-weight: 500;
                line-height: normal;
                font-size: 14px;
                text-align: center;
                background: #FD226A;
                box-shadow: 0px 10px 30px rgba(253, 34, 106, 0.5);
                border-radius: 34px;
                transition: 0.3s ease;
                &:hover{
                    background: #FD226A;
                    box-shadow: none;
                }
            }
            &-bottom {
                padding-top: 170px;
                font-family: 'Open Sans Regular';
                line-height: 22px;
                font-size: 14px;
                text-align: right;
                color: #FFFFFF;
                opacity: 0.35;
                @media(max-width: 767px) {
                    padding-top: 80px;
                    padding-bottom: 30px;
                }
            }
        }
    }
}
